#_32VlY {
  margin: 0 auto;
  padding: 20px 10px 0px;
}

#_32VlY>.Tdn75 {
  display: none;
}

#_32VlY>h1, #_32VlY>.Tdn75>a{
  color: var(--cd-font-color);
}

#_32VlY>.afHNY>div>div {
  width: 35px;
  height: 5px;
  margin: 6px 0;
  background-color: var(--cd-font-color);
}

/* to desktop */

@media (min-width: 768px) { 
  #_32VlY>.Tdn75 {
    display: inherit;
  }
  #_32VlY>.afHNY {
    display: none;
  }
}
